@import url(https://fonts.googleapis.com/css2?family=Montserrat);
body {
  min-height: 100vh;
  display: flex;
  flex-direction: column;

  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin:0;
  padding: 0;
  font-family: Poppins,"Montserrat",Century Gothic;
}

html, body {
  width: 100%;
  height: 100%;
}

.fade-enter {
  width: 100%;
  
  position: absolute;
  top:50px;
  opacity: 0;
  z-index: 1000;
}

.fade-enter.fade-enter-active {
  width: 100%;
  height: 2000px;
  background-color: white;
  position: absolute;
  top:50px;
  opacity: 1;
  transition: opacity 150ms ease-in;
}

::-webkit-scrollbar {
  background-color: white;
  width: 0;
}
#navbar {
    overflow: hidden;
    height: 50px;
    width: 100%;
    background-color: #333;
}

#navbar div div {
    display: block;
    color: white;
    text-align: center;
    padding: 14px 16px;
    text-decoration: none;
}

#navbar div:hover {
    background-color: #111;
}

#navbar div {
    float: left;
}
#header {
    font-family: Poppins;
    margin: 33px 0px 33px 33px;
}

.right {
    -webkit-transform: scaleX(-1);
            transform: scaleX(-1);
    float:left;
    position: absolute;
    right:25px;
    margin-top: 45px;
}

.left {
    position: absolute;
    right:65px;
    margin-top: 45px;
}

#header-wrapper .nav-item img {
    height:25px;
    float:right;
}

#header-wrapper {
    height:114px;
}

.buttons {
    float: right;
}
* {
    margin:0px;
    padding: 0px;
}

.day {
    position: relative;
    top: 10px;
    font-family: Poppins;
    font-weight: 100;
    font-size: 20px;
    margin-left: 20px;
    line-height: 0px;
    color: #3d3d3d;
}

.dayNum {
    height: 140px;
}

.dayMobile {
    visibility: hidden;
}

.red {
    width: 49px;
    height: 49px;
    border-radius: 50%;
    text-align: center;
    background-color: red;
    color: white;
    font-family: Poppins;
    font-size: 40px;
    font-weight: 700;
    line-height: 50px;
}

.num {
    font-size: 40px;
    font-weight: 700;
    margin-left: 20px;
    line-height: 0px;
    line-height: 50px;
}

.content {
    word-wrap: break-word;
    margin:5px;
    border-right: transparent;
    width: 95%;
    height: 100%;
    vertical-align: top;
    overflow: hidden;
    white-space: pre-wrap;
}

@media screen and (min-width: 1100px) {
    #tableFlex table  {
        border-collapse: collapse;
        width:100%;
        table-layout: fixed;
    }
    
    #dayPanel {
        border-top: 1px solid #bbbbbb;

    }

    #contentTable td {
        border-top:1px solid transparent;
        height:51vw;
    }
    
    #tableFlex table tr td {
        border:1px solid #bbbbbb;
        border-top: 0px solid transparent;
    }
}

@media screen and (max-width: 1100px) {
    .day {
        visibility: hidden;
    }
    .dayMobile {
        font-size: 20px;
        position: relative;
        top:10px;
        visibility: visible;
        margin-left: 20px;
    }
    .num {
        position: relative;
        top:20px;
    }
    .dayNum {
        padding: 0;
        margin:0;
        width: 100%;
    }
    .content {
        padding: 0;
        margin:5px;
        width: 100%;
    }
    
    #tableFlex {
        display: flex;
        overflow-x: hidden;
    }
    #tableFlex table {
        border-collapse: collapse;
    }

    #dayTable {
        float: left;
        width: 40%;
    }
    #dayPanel {
        position: relative;
        right:1px;
        float: left;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    #dayPanel td {
        min-height: 228px;
        border:1px solid #bbbbbb;
        border-bottom: 0px;
    }

    #contentTable {
        float: right;
        width:60%;
    }
    #contentPanel {
        float: right;
        width: 100%;
        display: flex;
        flex-direction: column;
    }
    #contentPanel td {
        min-height: 228px;
        border:1px solid #bbbbbb;
        border-right: 0px;
        border-left: 0px;
        border-bottom: 0px;
    }
}
.banner-panel {
    width: 100%;
    height: 7%;
    position: fixed;
    background-color: #333;
    top: 93%;       
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 0 -5px 5px rgba(138, 0, 0, 0.08);
}

.banner-text {
    color: white;
    align-self: center;
    font-size: 20px;
    font-weight: 500;
}
.event-wrapper {
    border-bottom: 1px #bbbbbb solid;
}
.event-item {
    height:150px;
    width: 100%;
    border-top:1px #bbbbbb solid;
}

.event-info {
    position: absolute;
    right:15px;
    overflow:hidden;
    float: right;
    margin-top: 55px;
    font-size: 17pt;
}

.event-name {
    white-space: nowrap;
    overflow:hidden;
    text-overflow: ellipsis;
    width: calc(100% - 120px);
    font-size: 20pt;
    margin-top: 50px;
    margin-left: 15px;
    margin-right: 250px;
    float: left;
}

.event-time {
    overflow:hidden;
    position: absolute;
    margin-left: 15px;
    margin-top: 83px;
    float: left;
    color: #3d3d3d;
}

@media screen and (min-width: 700px) {
    .event-time, .event-name {
        margin-left: 50px;
    }
    .event-info {
        margin-right: 50px;
    }
}

footer {
    width: 100%;
    height:100px;
    background-color: #333;
}

.footer-item {
    color:rgb(121, 119, 119);
    position: relative;
    top:20px;
    margin-left: 20px;
    text-decoration: none;
    transition: 0.1s;
}

.footer-item:hover {
    text-decoration: underline;
    color:white;
}
