footer {
    width: 100%;
    height:100px;
    background-color: #333;
}

.footer-item {
    color:rgb(121, 119, 119);
    position: relative;
    top:20px;
    margin-left: 20px;
    text-decoration: none;
    transition: 0.1s;
}

.footer-item:hover {
    text-decoration: underline;
    color:white;
}